import React, { Component } from 'react';

class Prices extends Component {
	state = {
		currency: 'USD'
	};
	render() {
		const { bpi } = this.props;
		return (
			<div>
				<ul className="list-group">
					<li className="list-group-item">Bitcoin rate for {bpi.USD.description}:</li>
				</ul>
			</div>
		);
	}
}

export default Prices;
